import React, { Component } from "react";

class TodoItem extends Component {
  constructor() {
    super();
    this.state = {
      descHeight: 0,
    };
  }

  componentDidMount() {
    this.setState({ descHeight: this.divRef.clientHeight });
    
  }

  render() {
    return (
      <div className="todo-item" onClick={() => this.props.handleOnclick(this.props.item.id)}>
        <h3 className="todo-heading">{this.props.item.heading}</h3>
        <p className="todo-desc" ref={(element) => (this.divRef = element)}>
          {this.props.item.desc}
        </p>
        <div className="todo-date">
          {this.props.item.date.toLocaleDateString()}
        </div>
        <div className="todo-time">
          {this.props.item.time.from.toLocaleTimeString().slice(0, 5)} -{" "}
          {this.props.item.time.to.toLocaleTimeString().slice(0, 5)}
        </div>
      </div>
    );
  }
}

export default TodoItem;
