import React, { Component } from "react";

class Modal extends Component {
  constructor() {
    super();
    this.state = {
      open: false,
    };
  }

  render() {
    return (
      <div
        className={
          this.props.item.show ? "modal modal-show" : "modal modal-hide"
        }
        onClick={(event) =>
          this.props.handleOutclick(event, this.props.item.id)
        }
      >
        <div className="modal-content">
          <h3 className="todo-heading">{this.props.item.heading}</h3>
          <span
            className="close"
            onClick={() => this.props.handleOnclick(this.props.item.id)}
          >
            &times;
          </span>
          <p className="todo-desc" ref={(element) => (this.divRef = element)}>
            {this.props.item.desc}
          </p>
          <div className="todo-date">
            {this.props.item.date.toLocaleDateString()}
          </div>
          <div className="todo-time">
            {this.props.item.time.from.toLocaleTimeString().slice(0, 5)} -{" "}
            {this.props.item.time.to.toLocaleTimeString().slice(0, 5)}
          </div>
        </div>
      </div>
    );
  }
}

export default Modal;
