import React, { Component } from "react";
import TodoItem from "./components/TodoItem";
import todosData from "./todosData";
import Modal from "./components/Modal";
import "./Todo2.css";

class Todo2 extends Component {
  constructor() {
    super();
    const td = todosData.map((item) => {
      item.show = false;
      return item;
    });
    this.state = {
      todos: td,
    };
    this.handleOnclick = this.handleOnclick.bind(this);
    this.handleOutclick = this.handleOutclick.bind(this);
  }

  handleOnclick(id) {
    this.setState((prevState) => {
      const updatedTodos = prevState.todos.map((todo) => {
        if (todo.id === id) {
          todo.show = !todo.show;
        }
        return todo;
      });
      return updatedTodos;
    });
  }

  handleOutclick(event, id) {
    if (event.target.className === "modal modal-show") {
      this.handleOnclick(id);
    }
  }

  render() {
    const todoItems = this.state.todos.map((item) => (
      <TodoItem key={item.id} item={item} handleOnclick={this.handleOnclick} />
    ));
    const todoModals = this.state.todos.map((item) => (
      <Modal
        key={item.id}
        item={item}
        handleOnclick={this.handleOnclick}
        handleOutclick={this.handleOutclick}
      />
    ));

    return (
      <div className="todos">
        <h1>Work</h1>
        {todoItems}
        {todoModals}
      </div>
    );
  }
}

export default Todo2;
